<template>
  <div class="chapter">
    <!-- 头部 -->
    <BookDetailHeader :title="$route.query.bookname" />
    <!-- 标题 -->
    <h3 class="btn-group-cell">
      <span>目录</span>
    </h3>
    <!-- 章节部分 -->
    <div class="chapter-contain">
      <h3>
        <span>共{{ chapters.length }}章</span>
      </h3>
      <ul>
        <li v-for="(item, index) in chapters" :key="index">
          <p>
            <span @click="toRead(index)">
              {{ item.title }}
            </span>
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { mapMutations } from "vuex";
import BookDetailHeader from "@/components/BookDetailHeader.vue";
export default {
  components: {
    BookDetailHeader,
  },
  data() {
    return {
      chapters: JSON.parse(window.localStorage.getItem("allChapters")).chapters,
      timer: null,
      id: this.$route.params.id,
      clickIndex: 0,
    };
  },
  methods: {
    ...mapMutations(["getAllChapter"]),
    toRead(i) {
      this.clickIndex = i;
      console.log(this.clickIndex);
      if (this.timer) {
        clearTimeout(this.timer);
      }
      Toast("正在获取，请稍后 Thanks♪(･ω･)ﾉ");
      this.timer = setTimeout(() => {
        this.$router.push({
          path: "/bookDetail/" + this.id + "/read",
          query: { bookName: this.$route.query.bookname },
        });
      }, 1000);
    },
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == "Read") {
      localStorage.setItem(
        "allChapters",
        JSON.stringify({
          chapters: this.chapters,
          index: this.clickIndex,
        })
      );
      this.getAllChapter({
        chapters: this.chapters,
        index: this.clickIndex,
      });
      next();
    } else {
      next();
    }
  },
};
</script>

<style lang="less">
.chapter {
  padding-top: 50px;
  .btn-group-cell {
    height: 45px;
    text-align: center;
    line-height: 45px;
    border-bottom: 1px solid red;
    margin-bottom: 5px;
    span {
      font-size: 14px;
      color: red;
    }
  }
  .chapter-contain {
    h3 {
      height: 44px;
      line-height: 28px;
      font-weight: 600;
      padding: 8px 16px;
      box-sizing: border-box;
    }
    ul {
      li {
        p {
          padding: 12px 32px 12px 16px;
          box-sizing: border-box;
          span {
            display: block;
            font-size: 14px;
            font-family: sans-serif;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
        &::after {
          display: block;
          margin-top: -1px;
          margin-left: 1rem;
          content: "";
          transform: scaleY(0.75);
          border-top: 1px solid #f0f1f2;
        }
      }
    }
  }
}
</style>